import React, { useState } from "react"
import '../scss/div-search-result.scss'
import BtnDown from './BtnDown'
//import DATABASE from './DATABASE'

function DivResultContend({toggle, objet, index, showChapName}){
    return <>
        <div className="title-img">
            <p>{objet.chapitres[index]}</p>
            <p className="mat-name">{showChapName ? objet.matiere : "détails"}</p>
            <button className="span-anim" onClick={toggle}>
                <svg  viewBox="0 0 100 100" >
                    <path id="Forme_1" data-name="Forme 1" d="M0.424,90.265l-0.848-.53,50-80,0.848,0.53Z"/>
                    <path id="Forme_2" data-name="Forme 2" d="M100.424,89.735l-0.848.53-50-80,0.848-.53Z"/>
                </svg>
            </button>
        </div>
        <div className="description-container">
            <p className="description">{objet.description[index] !== undefined ? objet.description[index] : objet.chapitres[index]}</p>
            <BtnDown object={objet} index={index}/>
        </div>
    </>
}

function Fade({visible, children, showChapName}){
    let className = 'div-search-result'
    ///////retirer toutes les classes 'div-has-click'
    const divSearch = document.querySelectorAll('.div-search-result')
    for(let i=0; i<divSearch.length; i++){
        if(divSearch[i].classList.contains('div-has-click')){
            divSearch[i].classList.remove('div-has-click')
        } 
    }

    if(!visible){
        className += ' div-has-click'
    }

    return <div className={className}>
        {children}
    </div>
}

const DivSearchResult = React.memo(({objet, index, showChapName}) => {
    const [open, setOpen] = useState(true)
    const toggle = () => setOpen(o => !o)

    return (
        <Fade visible={open} showChapName={showChapName}>
            <DivResultContend toggle={toggle} objet={objet} index={index} showChapName={showChapName}/>
        </Fade>
     );
})
 
export default DivSearchResult;

