import React from "react"
import DivSearchResult from "./DivSearchResult"


const removeAccent = (str) => {
    let accents    = 'ÀÁÂÃÄÅàáâãäåÒÓÔÕÕÖØòóôõöøÈÉÊËèéêëðÇçÐÌÍÎÏìíîïÙÚÛÜùúûüÑñŠšŸÿýŽž'
    let accentsOut = "AAAAAAaaaaaaOOOOOOOooooooEEEEeeeeeCcDIIIIiiiiUUUUuuuuNnSsYyyZz"
    str = str.split('')
    let strLen = str.length
    let i, x;
    for (i = 0; i < strLen; i++) {
      if ((x = accents.indexOf(str[i])) !== -1) {
        str[i] = accentsOut[x]
      }
    }
    return str.join('')
}

function SearchResult({filterText, Domain, DefaultAffiche, showChapName}){ /*Domain est le tableau de recherche*/
    let row = []
    filterText = removeAccent(filterText)
    filterText = filterText.toLowerCase()

    if(DefaultAffiche){
        Domain.forEach((object) => {
            object.chapitres.forEach(function(chap, index){
                let lowerChap = chap.toLowerCase()
                lowerChap = removeAccent(lowerChap)
                if(lowerChap.indexOf(filterText) !== -1){
                    row.push(<DivSearchResult objet={object} index={index} showChapName={showChapName} key={chap}/>)
                }
            })
        })
    }else{
        Domain.forEach((object) => {
            if(filterText === ''){
                return
            }
            object.chapitres.forEach(function(chap, index){
                let lowerChap = chap.toLowerCase()
                lowerChap = removeAccent(lowerChap)
                if(lowerChap.indexOf(filterText) === -1){
                    return
                }
                row.push(<DivSearchResult objet={object} index={index} showChapName={showChapName} key={chap}/>)
            })
        })
    }
    
    return <div className="search-result-container">
        {row}
    </div>
}

export default SearchResult