import React from "react"
import Navigation from "../components/Navigation"
import FilterableSearch from "../components/FilterableSearch"
import DATABASE from "../components/DATABASE"

function GenerateDomain (matName, Database){
    let domain = []
    Database.forEach((object) => {
        if(object.matiere === matName){
            domain[0] = object
        }
    })
    return domain
}


class Analyse2 extends React.Component{

    render(){
        return(
            <FilterableSearch Domain={GenerateDomain("Analyse 2", DATABASE)} DefaultAffiche={true} showChapName={false}/>
        )
    }
}
