import React from "react"
import { NavLink } from "react-router-dom"
import "../scss/search-bar.scss"

function BtnHome({children}){
    return <NavLink exact to="/" className="link-home">
        <div>
            <svg  viewBox="0 0 100 100">
                <path id="Forme_1" data-name="Forme 1" d="M0.424,90.265l-0.848-.53,50-80,0.848,0.53Z"/>
                <path id="Forme_2" data-name="Forme 2" d="M100.424,89.735l-0.848.53-50-80,0.848-.53Z"/>
            </svg>
            <p>{children}</p>
        </div>
    </NavLink>
}

class SearchBar extends React.Component{
    constructor(props){
        super(props)
        this.changeText = this.changeText.bind(this)
        this.canselSearch = this.canselSearch.bind(this)
    }

    changeText(e){
        this.props.onHandleSearchChange(e.target.value)
    }
    canselSearch(){
        this.props.onHandleCanselChange()
    }
    
    render(){
        return(
            <>
            <div className="search-bar-container">
                <input type="text" placeholder="Rechercher..."  value={this.props.filterTex} onChange={this.changeText}/>
                <button onClick={this.canselSearch}>Annuler</button>
            </div>
            {this.props.showHomeButton === undefined ?  <BtnHome>Cours</BtnHome>: null}
            
            </>
        )
    }
}

export {SearchBar, BtnHome}