import React from "react"
import { BrowserRouter, Switch, Route } from "react-router-dom"
import {Analyse2, Autres, Basep, Cirlin, Eb, Eoe, Ep1, Franc, Info2, Machelec, Home, NotFound} from "./pages/Page"
import Evenement from "./components/Evenement"
import Contact from "./components/Contact"
import Download from "./pages/PageDownload"

function Cover(){
  const coverLoader = document.getElementById('cover-loader')
  const body = document.querySelector('body')
  coverLoader.remove()
  body.classList.remove('wait-load')
}

class App extends React.Component{
  
  componentDidMount(){
    Cover()
  }

  render(){
    return (
      <>
        <BrowserRouter>
          <Switch>
            <Route path="/" exact component={Home}/>
            <Route path="/index.html" exact component={Home}/>
            <Route path="/Analyse2" exact component={Analyse2}/>
            <Route path="/Autres" exact component={Autres}/>
            <Route path="/Basep" exact component={Basep}/>
            <Route path="/Cirlin" exact component={Cirlin}/>
            <Route path="/Eb" exact component={Eb}/>
            <Route path="/Eoe" exact component={Eoe}/>
            <Route path="/Ep1" exact component={Ep1}/>
            <Route path="/Franc" exact component={Franc}/>
            <Route path="/Info2" exact component={Info2}/>
            <Route path="/Machelec" exact component={Machelec}/>
            <Route path="/evenement" exact component={Evenement}/>
            <Route path="/contact" exact component={Contact}/>
            <Route path="/Download" exact component={Download}/>
  
            <Route component={NotFound}/>
          </Switch>
        </BrowserRouter>
      </>
    )
  }
  
}

export default App;
