import React from "react"
import DATABASE from "../components/DATABASE"
import '../scss/page-download.scss'

function Link ({mat='', index, DATABASE, children}){
    let route = './cours/'
    let sauvMat = mat
    mat = mat.toLowerCase()
    mat = mat.split('')
    for(let i = 0; i < mat.length; i++){
        if(mat[i] === " "){
            mat[i] = '' ///enlever les espaces
        }
        if(mat[i] === "'"){
            mat[i] = "-" ///enlever les '
        }
    }
    mat = mat.join('')
    route = route + mat + '/'

    DATABASE.forEach((obj) => {
        if(obj.matiere === sauvMat){
            route = route + obj.fichiers[index]
        }
    })

    return(
        <a href={route} className="link-download">{children}</a>
    )
}

function FindChap({mat, index, DATABASE}){
    let chap = ''

    DATABASE.forEach((obj) => {
        if(obj.matiere === mat){
            chap = obj.chapitres[index]
        }
    })
    return <span><span>Fichier : </span>{chap === '' ? 'Aucun fichier' : chap}</span>
}

function Download(){
    const dataDownloadMat = document.getElementById('data-download-mat')
    const dataDownloadIndex = document.getElementById('data-download-index')

    let dataMat = dataDownloadMat.dataset['mat']
    let dataIndex = dataDownloadIndex.dataset['index']


    return(
        <div className="download-container">
            <p className="download-chap-name"><FindChap mat={dataMat} index={dataIndex} DATABASE={DATABASE}/></p>
            <Link mat={dataMat} index={dataIndex} DATABASE={DATABASE}>Proceder au téléchargement</Link>    
        </div>
    )
}
 
export default Download;