let imgAuthor = document.getElementById('img-author')
let asideAuthor = document.querySelector('.aside-author')

imgAuthor.addEventListener('mouseover', () =>{
    asideAuthor.style.opacity = '1';
})
imgAuthor.addEventListener('mouseout', () =>{
    asideAuthor.style.opacity = '0';
})
///////////////////////
////*Aside footer*/////
///////////////////////

///////////////////
/*hamburger click*/
///////////////////
const hamburgerContainer = document.querySelector('.hamburger-container')
const body = document.querySelector('body')

hamburgerContainer.addEventListener('click', function(e){
    e.stopPropagation()
    e.preventDefault()
    this.classList.toggle('hamburger-click')
    body.classList.toggle('has-navbar')

    let navLinks = document.querySelectorAll('.nav-links a')
    navLinks.forEach(function(link, i){
    link.style.transition = `transform ease-in-out .2s ${(i/10) + 0.1}s`
})
})
document.querySelector('#header').addEventListener('click', () => {
    hamburgerContainer.classList.remove('hamburger-click')
    body.classList.remove('has-navbar')
})
