import React from "react"
import { NavLink } from "react-router-dom"

class BtnDown extends React.Component {
    constructor(props){
        super(props)
        this.handleDownClick = this.handleDownClick.bind(this)
    }

    handleDownClick(){
        let {object, index} = this.props
        const dataDownloadMat = document.getElementById('data-download-mat')
        const dataDownloadIndex = document.getElementById('data-download-index')
        /*dataDownloadMat.innerText = `${object.matiere}`
        dataDownloadIndex.innerText = `${index}`*/

        dataDownloadMat.setAttribute('data-mat', object.matiere)
        dataDownloadIndex.setAttribute('data-index', index)
    }

    render() { 
        return ( 
            <button onClick={this.handleDownClick} className="link-down">
                <NavLink exact to="./Download">Télécharger</NavLink>
            </button>
        );
    }
}

export default BtnDown