import React from "react"
import {SearchBar} from "./SearchBar"
import SearchResult from "./SearchResult"


class FilterableSearch extends React.Component{
    constructor(props){
        super(props)
        this.state = {
            filterText: ''
        }
        this.handleSearchChange = this.handleSearchChange.bind(this)
        this.handleCanselChange = this.handleCanselChange.bind(this)
    }

    handleSearchChange(newText){
        this.setState({
            filterText: newText
        })
    }
    handleCanselChange(){
        this.setState({
            filterText: ''
        })
    }

    render(){
        
        return(
            <div className="search-content">
                <SearchBar 
                onHandleSearchChange={this.handleSearchChange}
                filterText={this.state.filterText}
                onHandleCanselChange={this.handleCanselChange}
                showHomeButton={this.props.showHomeButton}
                />
                <SearchResult filterText={this.state.filterText} Domain={this.props.Domain} DefaultAffiche={this.props.DefaultAffiche} showChapName={this.props.showChapName}/>
            </div>
        )
    }
}

export default FilterableSearch